import React, { useState } from 'react';
import { Modal, Button, Form, Carousel } from 'react-bootstrap';
import '../styles.css';

const rooms = [
  {
    id: 1,
    name: ' 1',
    description: '',
    images: [
      'https://via.placeholder.com/600x400?text=Room+1+Image+1',
      'https://via.placeholder.com/600x400?text=Room+1+Image+2',
      'https://via.placeholder.com/600x400?text=Room+1+Image+3'
    ],
    features: ', , '
  },
  {
    id: 2,
    name: ' 2',
    description: ' ',
    images: [
      'https://via.placeholder.com/600x400?text=Room+2+Image+1',
      'https://via.placeholder.com/600x400?text=Room+2+Image+2',
      'https://via.placeholder.com/600x400?text=Room+2+Image+3'
    ],
    features: ', '
  },
  {
    id: 3,
    name: ' 3',
    description: ' ',
    images: [
      'https://via.placeholder.com/600x400?text=Room+3+Image+1',
      'https://via.placeholder.com/600x400?text=Room+3+Image+2',
      'https://via.placeholder.com/600x400?text=Room+3+Image+3'
    ],
    features: ', '
  },
];

function Rooms() {
  const [selectedRoom, setSelectedRoom] = useState(null); // 
  const [formData, setFormData] = useState({
    name: '',
    checkInDate: '',
    checkOutDate: '',
    guests: 1,
  });
  const [showModal, setShowModal] = useState(false); //  Modal

  const handleRoomSelect = (room) => {
    setSelectedRoom(room);
    setShowModal(true); //  Modal 
  };

  const handleFormChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    alert(` "${selectedRoom.name}" !`);
    setShowModal(false); //  Modal 
  };

  return (
    <div className="container my-5">
      <h1 className="text-center mb-4"></h1>
      <div className="row">
        {/*  */}
        {rooms.map((room) => (
          <div key={room.id} className="col-md-4 my-3">
            <div className="card">
              <Carousel>
                {room.images.map((image, index) => (
                  <Carousel.Item key={index}>
                    <img
                      src={image}
                      className="d-block w-100"
                      alt={`room-${room.id}-image-${index}`}
                    />
                  </Carousel.Item>
                ))}
              </Carousel>
              <div className="card-body">
                <h5 className="card-title">{room.name}</h5>
                <Button
                  onClick={() => handleRoomSelect(room)}
                  className="btn btn-primary"
                >
                  
                </Button>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Modal  */}
      <Modal show={showModal} onHide={() => setShowModal(false)} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>: {selectedRoom?.name}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {/*  Modal */}
          <div className="room-details mb-4">
            <h5></h5>
            <Carousel>
              {selectedRoom?.images.map((image, index) => (
                <Carousel.Item key={index}>
                  <img
                    src={image}
                    className="d-block w-100"
                    alt={`room-${selectedRoom.id}-image-${index}`}
                  />
                </Carousel.Item>
              ))}
            </Carousel>
            <p><strong>:</strong> {selectedRoom?.description}</p>
            <p><strong>:</strong> {selectedRoom?.features}</p>
          </div>

          {/*  */}
          <Form onSubmit={handleSubmit}>
            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Control
                type="text"
                name="name"
                value={formData.name}
                onChange={handleFormChange}
                required
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Control
                type="date"
                name="checkInDate"
                value={formData.checkInDate}
                onChange={handleFormChange}
                required
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Control
                type="date"
                name="checkOutDate"
                value={formData.checkOutDate}
                onChange={handleFormChange}
                required
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Control
                type="number"
                name="guests"
                value={formData.guests}
                onChange={handleFormChange}
                min="1"
                required
              />
            </Form.Group>

            <Button variant="success" type="submit">
              
            </Button>
          </Form>
        </Modal.Body>
      </Modal>
    </div>
  );
}

export default Rooms;
